.addblog {
  padding-top: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
.addblog .addblogh1 {
  text-align: center;
  color: rgb(248, 95, 120);
  font-size: 40px;
}
.addblog label {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}
.addblog input,
.addblog textarea {
  resize: none;
  margin-bottom: 40px;
  width: 300px;
  padding: 10px;
  font-size: 15px;
  font-weight: 500;
}
.addblog textarea {
  height: 100px;
}
.addblog input {
  height: 40px;
}
.addblog button {
  width: 100%;
  height: 30px;
  display: flex;
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 22px;
  cursor: pointer;
  color: #fff;
  outline: none;
  border-radius: 8px;
  border: 0;
  border: 2px solid rgba(253, 113, 136, 0.605);
  background-color: transparent;
  color: rgb(248, 95, 120);
  text-decoration: none;
}/*# sourceMappingURL=addblog.css.map */